.App {
  font-family: 'Courier New', Courier, monospace;
  text-align: center;
}

.App-header {
  text-align: center;
  font-size: 40px;
  font-weight: bold;
  height: 100px;
  padding: 20px;
}

.App-intro {
  font-size: large;
  padding: 20px;
}

.deleteButton {
  visibility: hidden;
  float: right;
  margin: 5px;
  font-size: 20px;
  cursor: pointer;
  color: red;
}

.tile:hover .deleteButton {
  visibility: visible;
}

.tile {
  height: 140px;
  width: 140px;
  margin: 10px;
  background: rgb(255, 255, 186);
  float: left;
  font-size: 11px;
  text-align: left;
  padding: 5px;
  box-shadow: 4px 4px 5px 6px #ccc;
}

.buttonHolder {
  width: 100%;
  text-align: center;
  padding-bottom: 20px;
}

.notificationHolder {
  width: 100%;
  text-align: center;
  height: 20px;
}

.newIdeaButton {
  background: darkblue;
  color: white;
  border: none;
  font-size: 18px;
  cursor: pointer;
  margin-right: 10px;
  margin-left: 10px;
  padding: 10px;
  transition: 0.5s;
}

.newIdeaButton:hover {
  background: black;
  transition: 0.5s;
}

.input {
  border: 0;
  background: none;
  outline: none;
  margin-top:10px;
  width: 140px;
  font-size: 11px;
}

.input:focus {
  border: solid 1px lightgrey;
}

textarea {
  resize: none;
  height: 90px;
  font-size: 11px;
}